import * as ts from 'typescript';

export function forEachNodeInSubtree(
  node: ts.Node,
  cb: (n: ts.Node) => void,
  stopCond?: (n: ts.Node) => boolean
): void {
  cb(node);
  if (stopCond?.(node)) {
    return;
  }

  ts.forEachChild(node, (child) => {
    forEachNodeInSubtree(child, cb, stopCond);
  });
}
